.loader {
    width: 40px;
    height: 40px;
    position: relative;
    margin: auto;
    margin-top: 50%;
  }
  
  .bounce2 {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #6C757D;
    opacity: 0.9;
    position: absolute;
    top: 0;
    left: 0;
    animation: sk-bounce 2s infinite ease-in-out;
  }
  .bounce1 {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #495057;
    opacity: 0.9;
    position: absolute;
    top: 0;
    left: 0;
    animation: sk-bounce 2s infinite ease-in-out;
  }
  
  .bounce2 {
    animation-delay: -1s;
  }
  
  @keyframes sk-bounce {
    0%,
    100% {
      transform: scale(0);
    }
    50% {
      transform: scale(1);
    }
  }